const decoder = new TextDecoder('gbk');

export function goFetch(path, conf) {
  return fetch('/go-api' + path, conf);
}


/**
 * 方便跨域请求
 */
export function cors(url) {
  return fetch('/go-api/play/cors?' + url);
}


/**
 * 获取股票公告
 */
export async function fetchNotices(symbol, size = 5) {
  const url = `http://data.eastmoney.com/notices/getdata.ashx?StockCode=${symbol}&CodeType=1&PageIndex=1&PageSize=${size}&jsObj=a`;
  const res = await cors(url);
  const buf = await res.arrayBuffer();
  const dataView = new DataView(buf);

  const text = decoder.decode(dataView);
  return JSON.parse(text.slice(7, -1));
}

export function getToken() {
  return sessionStorage.token;
}

export function setToken(token) {
  sessionStorage.token = token;
}
