import React from "react";
import 'regenerator-runtime/runtime';

import {cors} from "../../../api/misc";
import StockChart from './stock/StockChart';

class Comp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  componentDidMount() {
    this.fetchStock();
  }

  async fetchStock() {
    const {symbol, market} = this.props.stock;
    const res = await cors(`http://emweb.securities.eastmoney.com/PC_HSF10/FinanceAnalysis/MainTargetAjax?code=${market}${symbol}&type=1`);
    const {Result} = await res.json();
    this.setState({stock: Result.reverse()})
  }

  renderChart({caption, fields, colors}) {
    const {stock} = this.state;
    if (!stock || !stock.length) {
      return null;
    }
    return <StockChart caption={caption} fields={fields}
                       data={stock} colors={colors}/>
  }


  render() {
    return <React.Fragment>
      <div ref={el => this.$chart = el}/>

      {
        this.renderChart({
          caption: '净资产收益率(%)',
          fields: [{
            prop: 'jqjzcsyl',
            propTitle: "收益率",
            propFormat: formatRatio,
          }],
          colors: ['violet']
        })
      }

      {
        this.renderChart({
          caption: '归属净利润(万元)',
          fields: [
            {
              prop: 'gsjlr',
              propTitle: "归属净利润",
              propFormat: formatMoney,
            },
            {
              prop: 'kfjlr',
              propTitle: "扣非净利润",
              propFormat: formatMoney,
            }
          ]
        })
      }

      {
        this.renderChart({
          caption: '收入同比增长率(%)',
          fields: [
            {
              prop: 'yyzsrtbzz',
              propTitle: '营业总收入同比增长率',
              propFormat: formatRatio,
            },
            {
              prop: 'gsjlrtbzz',
              propTitle: '归属净利润同比增长率',
              propFormat: formatRatio,
            }
          ]

        })
      }

    </React.Fragment>
  }
}

const factor = {'万': 1, '亿': 10000};

function formatMoney(value) {
  const f = factor[value.slice(-1)] || 1;
  return f * value.replace(/[^\d.]/g, '');
}

function formatRatio(v) {
  return +v;
}

export default Comp
