import React from "react";

/**
 * 股票解禁数据
 */
const StockDesterilization = ({stock, summary, items}) => {

  if (!items || !items.length) {
    return null;
  }
  const body = items ? items.map(v => {
    return <tr key={v.date}>
      <td>{v.date}</td>
      <td>{v.amount}</td>
      <td>{v.unit_price}</td>
      <td>{v.market_value}</td>
      <td>{v.ratio}</td>
    </tr>
  }) : void 0;

  return <div className="card">
    <header className="card-header">
      <p className="card-header-title">
        {stock.name}: 解禁状况
      </p>
      <a href="#" className="card-header-icon" aria-label="more options">
      <span className="icon">
        <i className="fa fa-angle-down" aria-hidden="true"/>
      </span>
      </a>
    </header>
    <div className="card-content">
      <div className="content">
        <table className='table is-bordered is-narrow'>
          <caption>{summary}</caption>
          <thead>
          <tr>
            <th>日期</th>
            <th>股数(万)</th>
            <th>单价</th>
            <th>总市值</th>
            <th>占比</th>
          </tr>
          </thead>
          <tbody>
          {body}
          </tbody>
        </table>
      </div>
    </div>
  </div>
};

export default StockDesterilization;
