import React from "react";

import StockIntro from './Summary';
import StockDesterilization from './Desterilization';
import Chart from '../Chart';
import Notices from './Notices';

import Spinner from '../../../widgets/Spinner';
import {fetchNotices, goFetch} from "../../../../api/misc";

class Detail extends React.Component {

  constructor(props) {
    super(props);
    this.state = {loadingStock: true}
  }

  async componentDidMount() {
    const {stock} = this.props;
    const {data} = await (await goFetch(`/stock/${stock.symbol}/desterilization`)).json();
    const {data: notices} = await fetchNotices(stock.symbol);
    this.setState({
      desterilization: data,
      notices: notices.map(n => {
        return {
          date: new Date(n.NOTICEDATE).toLocaleDateString(),
          title: n.NOTICETITLE,
          url: n.Url,
          type: n.ANN_RELCOLUMNS[0].COLUMNNAME,
        }
      }),
      loadingStock: false
    })
  }

  async componentWillReceiveProps(nextProps) {
    if (this.props.stock.symbol === nextProps.stock.symbol) {
      return;
    }
    // 切换股票了，需要重新加载数据
    const {stock} = nextProps;
    this.setState({
      loadingStock: true,
    });
    const {data} = await (await goFetch(`/stock/${stock.symbol}/desterilization`)).json();
    const {data: notices} = await fetchNotices(stock.symbol);
    this.setState({
      desterilization: data,
      notices: notices.map(n => {
        return {
          date: new Date(n.NOTICEDATE).toLocaleDateString(),
          title: n.NOTICETITLE,
          url: n.Url,
          type: n.ANN_RELCOLUMNS[0].COLUMNNAME,
        }
      }),
      loadingStock: false
    })
  }

  shouldComponentUpdate(nextProps, nextState) {
    return this.props.stock.symbol !== nextProps.stock.symbol
      || this.props.chosen !== nextProps.chosen
      || this.state.loadingStock !== nextState.loadingStock;
  }

  renderDetail() {
    const {stock, addToCollection, chosen} = this.props;
    const {desterilization, loadingStock, notices} = this.state;

    if (loadingStock) {
      return <Spinner/>
    }
    return <div>
      <StockIntro stock={stock} addToCollection={addToCollection} chosen={chosen}/>
      <StockDesterilization {...desterilization}/>
      <Chart stock={stock}/>
      {stock && <Notices stock={stock} notices={notices}/>}
    </div>
  }

  render() {
    return this.renderDetail()
  }
}

export default Detail;
